___CSS_LOADER_EXPORT___.push([module.id, `body {
    box-sizing: border-box;
    font-size: 14;
    background: #e9e9e9;
    font-family: sans-serif;
}

.comment-box,
.post-comment .list {
    background: #fff;
    border-radius: 6px;
    box-shadow: 0 2px 2px #002;
}
.post-comment {
    align-items: center;
    padding: 0 20px 0 0;
}

.comment-session {
    width: 650px;
    height: auto;
    margin: 0 auto;
}

.post-comment .list {
    width: 100%;
    margin: 12px;
}

.post-comment .list .user {
    display: flex;
    padding: 8px;
    overflow: hidden;
}

.post-comment .list .user .user-image {
    height: 38px;
    width: 38px;
    margin-right: 10px;
    border-radius: 50%;
}

.post-comment .list .user .user-meta {
    margin-right: auto;
    text-align: left;
}

.post-comment .list .user img {
    height: 38px;
    width: 38px;
    margin-right: 10px;
    border-radius: 50%;
}

.comment-session .name {
    text-transform: capitalize;
    font-weight: 500;
}

.post-comment .list .date {
    font-size: 12px;
}

.post-comment .comment-post {
    text-align: left;
    padding: 0 0 15px 58px;
}

/* Comment Box */
.comment-box {
    align-items: center;
    padding: 10px;
    overflow: hidden;
}

.comment-box .user {
    display: flex;
    width: min-content;
}

.comment-box .image i {
    width: 24px;
    height: 24px;
    margin-right: 10px;
    border-radius: 50%;
}

.comment-box textarea {
    background: #eee;
    width: -webkit-fill-available;
    height: 152px;
    margin: 10px 0;
    padding: 10px;
    resize: inherit;
    border-radius: 5px;
    border: none;
    box-shadow: 0 0 0 0.5px #0003;
}

.comment-box textarea:focus-visible {
    box-shadow: inset 0 0 0 1px #009cff;
    outline: none;
}

.comment-box .comment-submit {
    padding: 6px 10px;
    border: none;
    background: #009cff;
    color: #eee;
    cursor: pointer;
    border-radius: 3px;
}

.comment-box .comment-submit:hover {
    background: #0076bf;
}`, "",{"version":3,"sources":["webpack://./src/styles/comment.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;;IAEI,gBAAgB;IAChB,kBAAkB;IAClB,0BAA0B;AAC9B;AACA;IACI,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,cAAc;AAClB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,0BAA0B;IAC1B,gBAAgB;AACpB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,gBAAgB;IAChB,sBAAsB;AAC1B;;AAEA,gBAAgB;AAChB;IACI,mBAAmB;IACnB,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;IAChB,6BAA6B;IAC7B,aAAa;IACb,cAAc;IACd,aAAa;IACb,eAAe;IACf,kBAAkB;IAClB,YAAY;IACZ,6BAA6B;AACjC;;AAEA;IACI,mCAAmC;IACnC,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,mBAAmB;IACnB,WAAW;IACX,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;AACvB","sourcesContent":["body {\r\n    box-sizing: border-box;\r\n    font-size: 14;\r\n    background: #e9e9e9;\r\n    font-family: sans-serif;\r\n}\r\n\r\n.comment-box,\r\n.post-comment .list {\r\n    background: #fff;\r\n    border-radius: 6px;\r\n    box-shadow: 0 2px 2px #002;\r\n}\r\n.post-comment {\r\n    align-items: center;\r\n    padding: 0 20px 0 0;\r\n}\r\n\r\n.comment-session {\r\n    width: 650px;\r\n    height: auto;\r\n    margin: 0 auto;\r\n}\r\n\r\n.post-comment .list {\r\n    width: 100%;\r\n    margin: 12px;\r\n}\r\n\r\n.post-comment .list .user {\r\n    display: flex;\r\n    padding: 8px;\r\n    overflow: hidden;\r\n}\r\n\r\n.post-comment .list .user .user-image {\r\n    height: 38px;\r\n    width: 38px;\r\n    margin-right: 10px;\r\n    border-radius: 50%;\r\n}\r\n\r\n.post-comment .list .user .user-meta {\r\n    margin-right: auto;\r\n    text-align: left;\r\n}\r\n\r\n.post-comment .list .user img {\r\n    height: 38px;\r\n    width: 38px;\r\n    margin-right: 1