___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
    font-family: sans-serif;
    box-sizing: border-box;
  }
  
  body {
    background: #fefefe;
  }
  
  /* Skip to Content */
  .skip-link {
    min-width: 44px;
    max-height: 44px;
    position: absolute;
    top: -40px;
    left: 0;
    background-color: #bf1722;
    color: white;
    padding: 8px;
    z-index: 500;
  }
  
  .skip-link:focus {
    color: white;
    top: 0;
  }
  
  /* App Bar */
  header {
    width: 100%;
    height: 125px;
    background: #2D3E50;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 100px;
    position: fixed;
    z-index: 100;
  }
  
  .logo {
    min-width: 44px;
    min-height: 44px;
    font-size: 28px;
    color: #fefefe;
    font-weight: bold;
    transition: 0.5s;
  }
  
  .logo:hover {
    color: black;
  }
  
  .hamburger {
    min-width: 44px;
    min-height: 44px;
    background-color: #47586b;
    display: none;
  }
  
  .nav-bar ul {
    display: flex;
  }
  
  .nav-bar ul li a {
    width: 150px;
    height: -100px;
    font-size: 40px;
    display: block;
    color: #fefefe;
    font-size: 20px;
    padding: 20px 30px;
    border-radius: 50px;
    transition: 0.2s;
    margin: 0 5px;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  .nav-bar ul li a:hover {
    color: #11101b;
    background: #fefefe;
  }
  
  .nav-bar ul li a.open {
    max-height: 500px;
    height: 65px;
    font-size: 25px;
    padding: 20px 40px;
    color: #11101b;
    background: #fefefe;
  }
  
  /* Jumbotron/heros */
  .heroContainer {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #efefef;
  }
  
  .slide-container {
    position: relative;
    width: 1100px;
    height: 350px;
    border: 3px solid #ede6d6;
    box-shadow: 0 0 8px 2px rgba(0, 0, 0, 0.2);
  }
  
  .slide-container .slides {
    width: 100%;
    height: calc(100% - 40px);
    position: relative;
    overflow: hidden;
  }
  
  .slide-container .slides img {
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
  }
  
  .slide-container .slides img:not(.active) {
    top: 0;
    left: -100%;
  }
  
  span.next,
  span.prev {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 14px;
    color: #eee;
    font-size: 24px;
    font-weight: bold;
    transition: 0.5s;
    border-radius: 3px;
    user-select: none;
    cursor: pointer;
    z-index: 1;
  }
  
  span.next {
    right: 20px;
  }
  
  span.prev {
    left: 20px;
  }
  
  span.next:hover,
  span.prev:hover {
    background-color: #ede6d6;
    opacity: 0.8;
    color: #222;
  }
  
  .dotsContainer {
    position: absolute;
    bottom: 5px;
    z-index: 3;
    left: 50%;
    transform: translateX(-50%);
  }
  
  .dotsContainer .dot {
    width: 15px;
    height: 15px;
    margin: 0px 2px;
    border: 3px solid #bbb;
    border-radius: 50%;
    display: inline-block;
    cursor: pointer;
    transition: background-color 0.6s ease;
  }
  
  .dotsContainer .active {
    background-color: #555;
  }
  
  @keyframes next1 {
    from {
      left: 0%
    }
  
    to {
      left: -100%;
    }
  }
  
  @keyframes next2 {
    from {
      left: 100%
    }
  
    to {
      left: 0%;
    }
  }
  
  @keyframes prev1 {
    from {
      left: 0%
    }
  
    to {
      left: 100%;
    }
  }
  
  @keyframes prev2 {
    from {
      left: -100%
    }
  
    to {
      left: 0%;
    }
  }
  
  /* Main content */
  main {
    justify-content: center;
    margin: 50px;
  }
  
  main>h2 {
    text-align: center;
    justify-content: center;
    align-items: center;
    font-size: 35px;
  }
  
  .main-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .card-container {
