    flex-wrap: wrap;
    margin-top: 50px;
  }
  
  .card {
    width: 325px;
    background-color: #fefefe;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
    margin: 20px;
  }
  
  .card img {
    width: 100%;
    height: auto;
    transition: 0.3s ease;
  }

  .card img:hover {
    transform: scale(1.1);
  }
  
  .card-content {
    padding: 16px;
  }
  
  .card-content h3 {
    font-size: 28px;
    margin-bottom: 8px;
  }

  .card-content h3 a {
    color: #11101b;
  }

  .card-content h3 a:hover {
    color: #89b0d9;
  }
  
  .card-content h4 {
    font-size: 14px;
    padding: 10px;
  }

  .card .rating {
    position: absolute;
    color: #f1f1f1;
    font-weight: bold;
    margin-top: -50px;
    padding: 4px 8px;
    border-radius: 0 8px 8px 0;
    background-color: #1b1b1b;
    z-index: 1;
  }

  .card .rating span {
    color: #ddd;
  }
  
  .card-content h4 i {
    text-align: end;
  }
  
  .card-content p {
    color: #666;
    font-size: 15px;
    line-height: 1.3;
  }

  .card-content > h4 > i > span {
    color: #11101b;
  }
  
  .card-content .btn-read {
    display: inline-block;
    padding: 8px 16px;
    background-color: #2D3E50;
    text-decoration: none;
    border-radius: 4px;
    color: #fefefe;
  }
  
  /* Modal Description */
  .modal-container {
    margin: auto;
    background: rgba(0, 0, 0, 0.2);
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    z-index: 100;
  }
  
  .modal-container.show {
    opacity: 1;
    pointer-events: auto;
  }
  
  .modal {
    background: #fff;
    width: 550px;
    max-width: 100%;
    border-radius: 0.5px;
  }
  
  .modal-header {
    display: flex;
    background: #41556a;
    color: white;
    padding: 20px 15px;
    border-top-left-radius: 0.5px;
    border-top-right-radius: 0.5px;
    justify-content: space-between;
    align-items: center;
  }
  
  .modal-header i {
    cursor: pointer;
  }
  
  .modal-content {
    padding: 20px 15px;
    line-height: 2rem;
    text-align: center;
  }
  
  .modal-content img {
    display: block;
    width: 25%;
    margin: 10px auto;
  }
  
  .modal-footer {
    border-top: 1px solid #ddd;
    padding: 20px 15px;
  }
  
  .modal-footer .btn-close {
    display: block;
    margin-left: auto;
    background: transparent;
    color: #333;
    outline: 1px solid #2D3E50;
    padding: 10px 30px;
    cursor: pointer;
    transition: all 0.3s;
    border-radius: 0.5px;
  }
  
  .modal-footer .btn-close:hover {
    background: #2D3E50;
    color: white;
    transition: 0.3s;
  }
  
  /* Footer */
  footer {
    position: absolute;
    background: #2D3E50;
    height: auto;
    width: 100vh;
    font-family: 'Quicksand', sans-serif;
    padding-top: 40px;
    color: white;
  }
  
  .footer-content {
    width: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
  }
  
  .footer-content h3 {
    font-size: 1.8rem;
    font-weight: 400;
    text-transform: capitalize;
    line-height: 3rem;
  }
  
  .footer-content p {
    max-width: 500px;
    margin: 10px auto;
    line-height: 28px;
    font-size: 16px;
  }
  
  .socials {
    font-size: 50px;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin: 1rem 0 3rem 0;
  }
  
  .socials li {
    margin: 0 10px;
  }
  
  .socials a {
    max-width: 100px;
    padding: 30px;
    width: 120px;
    height: 100px;
    text-decoration: none;
    color: white;
  }
  
  .socials a i {
    padding: 10px;
    font-size: 2.5rem;
    transition: color .4s ease;
  }
  
  .socials a:hover i {
    color: #89b0d9;
    transform: scale(106%);
  }
  
  #contact {
    width: 100%;
  }
  
  .footer-bottom {
    background: black;
    width: 100%;
    padding: 20px 0;
    text-align: center;
  }
  
  .footer-bottom p {
    font-size: 16px;
    word-spacing: 3px;
    text-transform: capitalize;
  }
  
  .footer-bottom span {
    text-transform: uppercase;
    opacity: .4;
    font-weight: 200;
  }
  
  
  /* On smaller screens, decrease text size */
  @media screen and (max-width: 300px) {
    .hamburger {
      display: block;
      cursor: pointer;
    }
  
