 }\r\n  \r\n  .slide-container {\r\n    position: relative;\r\n    width: 1100px;\r\n    height: 350px;\r\n    border: 3px solid #ede6d6;\r\n    box-shadow: 0 0 8px 2px rgba(0, 0, 0, 0.2);\r\n  }\r\n  \r\n  .slide-container .slides {\r\n    width: 100%;\r\n    height: calc(100% - 40px);\r\n    position: relative;\r\n    overflow: hidden;\r\n  }\r\n  \r\n  .slide-container .slides img {\r\n    width: 100%;\r\n    height: 100%;\r\n    position: absolute;\r\n    object-fit: cover;\r\n  }\r\n  \r\n  .slide-container .slides img:not(.active) {\r\n    top: 0;\r\n    left: -100%;\r\n  }\r\n  \r\n  span.next,\r\n  span.prev {\r\n    position: absolute;\r\n    top: 50%;\r\n    transform: translateY(-50%);\r\n    padding: 14px;\r\n    color: #eee;\r\n    font-size: 24px;\r\n    font-weight: bold;\r\n    transition: 0.5s;\r\n    border-radius: 3px;\r\n    user-select: none;\r\n    cursor: pointer;\r\n    z-index: 1;\r\n  }\r\n  \r\n  span.next {\r\n    right: 20px;\r\n  }\r\n  \r\n  span.prev {\r\n    left: 20px;\r\n  }\r\n  \r\n  span.next:hover,\r\n  span.prev:hover {\r\n    background-color: #ede6d6;\r\n    opacity: 0.8;\r\n    color: #222;\r\n  }\r\n  \r\n  .dotsContainer {\r\n    position: absolute;\r\n    bottom: 5px;\r\n    z-index: 3;\r\n    left: 50%;\r\n    transform: translateX(-50%);\r\n  }\r\n  \r\n  .dotsContainer .dot {\r\n    width: 15px;\r\n    height: 15px;\r\n    margin: 0px 2px;\r\n    border: 3px solid #bbb;\r\n    border-radius: 50%;\r\n    display: inline-block;\r\n    cursor: pointer;\r\n    transition: background-color 0.6s ease;\r\n  }\r\n  \r\n  .dotsContainer .active {\r\n    background-color: #555;\r\n  }\r\n  \r\n  @keyframes next1 {\r\n    from {\r\n      left: 0%\r\n    }\r\n  \r\n    to {\r\n      left: -100%;\r\n    }\r\n  }\r\n  \r\n  @keyframes next2 {\r\n    from {\r\n      left: 100%\r\n    }\r\n  \r\n    to {\r\n      left: 0%;\r\n    }\r\n  }\r\n  \r\n  @keyframes prev1 {\r\n    from {\r\n      left: 0%\r\n    }\r\n  \r\n    to {\r\n    